import * as React from "react";
import {Field, InjectedFormProps, reduxForm} from "redux-form";
import {Input} from "../common/FormsControls/FormsControls";
import {maxSymbol20, required} from "../../utils/validators/validators";
import {connect} from "react-redux";
import {login} from "../../redax/auth-reducer";
import {Navigate} from "react-router-dom";
import {AppStateType} from "../../redax/redux-store";
const s = require("./login.module.css")


export const maxSymbolValidate = maxSymbol20(26)


type LoginOwnPropsType = {
    captchaUrl: string | null
}

const LoginForm: React.FC<InjectedFormProps<LoginFormValidateType, LoginOwnPropsType> & LoginOwnPropsType> =
    ({handleSubmit, error, captchaUrl}) => {
    //  debugger
    return <div>
        <form onSubmit={handleSubmit}>
            <div>
                <Field component={Input} className={s.inp} validate={[required, maxSymbolValidate ]}
                       name={'email'} type="login" placeholder={'email'}/>
            </div>
            <div>
                <Field component={Input} className={s.inp} validate={[required, maxSymbolValidate ]}
                       name={'password'} type="password" placeholder={'password'}/>
            </div>
            <div>
                <Field component={'input'} type="checkbox" className={s.inp} name={'rememberMe'} />Remember me
            </div>

            { captchaUrl && <img src={captchaUrl} /> }
            { captchaUrl &&  <Field placeholder={'Write symbols from image'} type='text' component={Input} validate={[required]} className={s.inp} name={'captcha'} />}

            {
                error && <div className={s.formSummaryError}>
                {error}
            </div>
            }
            <div>
                <button className={s.inp}>Login</button>
            </div>
        </form>
    </div>

}

const LoginReduxForm = reduxForm<LoginFormValidateType, LoginOwnPropsType>({ form: 'login' }) (LoginForm)


type MapStateType = {
    captchaUrl: string | null
    isAuth: boolean
}
type MapDispatchType = {
    login: (email: string, password: string, rememberMe: boolean, captcha: string) => void
}
type LoginFormValidateType = {
    email: string
    password: string
    rememberMe: boolean
    captcha: string
}

const Login: React.FC<MapStateType & MapDispatchType> = (props) => {
    const onSubmit = (formData: LoginFormValidateType) => {
        props.login(formData.email, formData.password, formData.rememberMe, formData.captcha);
    }

    if (props.isAuth) {
        return <Navigate to={'/profile'}/>
    }
    return <div>
        <h1>LOGIN</h1>
        <LoginReduxForm captchaUrl={props.captchaUrl} onSubmit={onSubmit}/>
    </div>

}

const mapStateToProps = (state: AppStateType): MapStateType => ({
    captchaUrl: state.auth.captchaUrl,
    isAuth: state.auth.isAuth
})


export default connect(mapStateToProps, {login}) (Login)