import * as React from 'react';
import Header from "./Header";
import {connect} from "react-redux";
import {logout} from "../../redax/auth-reducer";
import {AppStateType} from "../../redax/redux-store";

type MapStateType = {
    isAuth: boolean | null
    login: string | null
}
type DispatchType = {
    logout: () => void
}

function HeaderContainer(props: MapStateType & DispatchType) {
    return (
        <>
            {/*{this.props.isFetching ? <Image/> : null}*/}
            <Header {...props} />
        </>
    )
}

const mapStateToProps = (state: AppStateType) => ({
    // isFetching: state.auth.isFetching,
    isAuth: state.auth.isAuth,
    login: state.auth.login,
})


export default connect<MapStateType, DispatchType, {}, AppStateType>(mapStateToProps, { logout }) (HeaderContainer);